<template>
  <el-row>
    <el-dialog v-model="editFormVisible" title="书架编辑框">
      <el-form label-width="auto" :label-position="right" :model="editFormData">
        <el-form-item label="书架名" :label-width="formLabelWidth">
          <el-input v-model="editFormData.bookShelfname" autocomplete="off" />
        </el-form-item>
        <el-form-item label="书架简介" :label-width="formLabelWidth">
          <el-input :autosize="{ minRows: 2, maxRows: 4 }" type="textarea" placeholder="请输入内容" v-model="editFormData.bookShelfProfile"></el-input>
        </el-form-item>
      </el-form>
      <template #footer>
        <span class="dialog-footer">
          <el-button @click="editFormVisible = false">取消</el-button>
          <el-button type="primary" @click="editOrAddConfirm">确认</el-button>
        </span>
      </template>
    </el-dialog> 
    <el-col :span="2">
      <BookshelfAside
      ref="child"
      @changeShelf="changeDataTable"
      @addNewShelf="addNewShelf"
      @reloadTable="reloadTable"/>
    </el-col>
    <el-col :span="22">
      <div style="width: 100%;height: 100%">
        <div>
          <el-button @click="reloadForm()">刷新记录</el-button>
        </div>
<!-- 展示框 -->
        <el-table @row-click="goToDetail" :data="tableData.slice((currentPage-1)*pageSize,currentPage*pageSize)" stripe border style="width: 100%">
          <el-table-column prop="bookname" label="图书名" sortable/>
          <el-table-column prop="author" label="作者" sortable/>
          <el-table-column prop="press" label="出版社" sortable/>
          <el-table-column prop="booknum" label="索书号" sortable/>
          <el-table-column fixed="right" label="操作" width="200">
            <template #default="scope">
              <div>
                <el-button @click.stop="deleteBookFromBookshelf(scope.row.bookid)">
                  移除图书
                </el-button>
              </div>
            </template>
          </el-table-column>
        </el-table>
<!-- 分页 -->
        <div>
          <el-pagination
              v-model:current-page="currentPage"
              :page-sizes="[5, 10, 20]"
              v-model:page-size="pageSize"
              layout="total, sizes, prev, pager, next, jumper"
              :total="total"
              @size-change="handleSizeChange"
              @current-change="handleCurrentChange">
          </el-pagination>
        </div>
      </div>
    </el-col>
  </el-row>
</template>

<script>

import Header from "@/components/Header";
import Aside from "@/components/Aside";
import axios from "axios";
import qs from "qs";
import BookshelfAside from "@/components/BookshelfAside.vue"
export default {
  name: 'HomeView',
  components: {
    Header,
    Aside,
    BookshelfAside
  },
  data(){
    return{
      tableData:[],
      currentPage:1,
      total: 20, 
      pageSize: 20, 
      editFormData:{
        bookShelfname:undefined,
        bookShelfProfile:undefined,
        work:undefined,
      },
      editFormVisible:false,
    }
  },
  methods:{
    addNewShelf(){
      this.editFormVisible = true;
      this.editFormData.work = 1;
    },
    reloadTable(){
      this.tableData = [];
    },
    editOrAddConfirm(){
      if(this.editFormData.work == 1)
      {
        let token = JSON.parse(localStorage.getItem("token"));
        axios.post("https://localhost/bookshelf/addNewBookShelf",qs.stringify({bookShelfname:this.editFormData.bookShelfname,bookShelfProfile:this.editFormData.bookShelfProfile,studentid:token.id})).then(res=>{
          if(res.data == true){
            this.$message({
              type:"success",
              message:"新增成功"
            })
            this.$refs.child.reloadShelves()
            this.editFormVisible = false;
            }
          else
            this.$message({
                        type:"error",
                        message:"新增失败"
                      })
        })
      }
    },
    deleteBookFromBookshelf(bookid){
      let currentBookshelfid = this.$refs.child.currentBookshelfid;
      axios.post("https://localhost/bookshelf/deletBookfromShelf",qs.stringify({bookid:bookid,shelfid:currentBookshelfid})).then(res=>{
        axios.post('https://localhost/bookshelf/getAllBookByBookshelfid',qs.stringify({bookshelfid:currentBookshelfid})).then(res=>{
          this.changeDataTable(res);
        })
      });
    },
    goToDetail(row){
     window.open("https://localhost/bookDetail?keyword="+encodeURI(row.bookid));
    },
    changeDataTable(val){
      this.tableData = val.data;
      this.tableData.forEach(element=>{
        axios.post("https://localhost/holdingInformation/getBooknumByBookid",qs.stringify({bookid:element.bookid})).then(res=>{
          element["booknum"] = res.data
        })
      })
    }
  },
  created(){
  }
}
</script>