<template>
  <div style="width: 100%;height: 100%">
    <div>
      <el-button @click="reloadForm()">刷新记录</el-button>
    </div>
<!-- 展示框 -->
    <el-table @row-click="goToDetail" :data="tableData.slice((currentPage-1)*pageSize,currentPage*pageSize)" stripe border style="width: 100%">
      <el-table-column prop="barcode" label="条码号" sortable/>
      <el-table-column prop="bookname" label="图书名" sortable/>
      <el-table-column label="借阅时间" width="200">
        <template #default="scope">
          <span>{{scope.row.borrowtime.getFullYear()}}-{{scope.row.borrowtime.getMonth() + 1}}-{{scope.row.borrowtime.getDate()}}</span>
        </template>
      </el-table-column>
      <el-table-column label="应还时间" width="200">
        <template #default="scope">
          <span>{{scope.row.duedate.getFullYear()}}-{{scope.row.duedate.getMonth() + 1}}-{{scope.row.duedate.getDate()}}</span>
        </template>
      </el-table-column>
      <el-table-column label="状态" width="200">
        <template #default="scope">
          <span v-if="scope.row.overtime && scope.row.loss == 0"><font color="red">超时</font></span>
          <span v-if="!scope.row.overtime && scope.row.loss == 0"><font color="green">正常</font></span>
          <span v-if="scope.row.loss == 1"><font color="red">挂失未缴费</font></span>
        </template>
      </el-table-column>
      <el-table-column fixed="right" label="操作" width="200">
        <template #default="scope">
          <div>
            <el-button v-if="scope.row.loss == 1" @click.stop="changeLossState(scope.row.borrowid)">
              取消挂失
            </el-button>
            <el-button v-if="scope.row.loss == 0" @click.stop="changeLossState(scope.row.borrowid)">
              图书挂失
            </el-button>
          </div>
        </template>
      </el-table-column>
    </el-table>
<!-- 分页 -->
    <div>
      <el-pagination
          v-model:current-page="currentPage"
          :page-sizes="[5, 10, 20]"
          v-model:page-size="pageSize"
          layout="total, sizes, prev, pager, next, jumper"
          :total="total"
          @size-change="handleSizeChange"
          @current-change="handleCurrentChange">
      </el-pagination>
    </div>
  </div>
</template>

<script>

import Header from "@/components/Header";
import Aside from "@/components/Aside";
import axios from "axios";
import qs from "qs";

export default {
  name: 'HomeView',
  components: {
    Header,
    Aside
  },
  data(){
    return{
      userid:undefined,
      tableData:[],
      currentPage:1,
      total: 20, 
      pageSize: 20, 
    }
  },
  methods:{
    reload(){
      axios.post('https://localhost/borrowHistory/getCurrentBorrowingByUserid',qs.stringify({userid:this.userid})).then(res=>{
        this.tableData = res.data;
        this.tableData.forEach(element=>{
          element.borrowtime = new Date(element.borrowtime);
          element.duedate = new Date(element.duedate);
          let now = Date.parse(Date());
          if(now < Date.parse(element.duedate)){
            element["overtime"] = false;
          }
          else{
            element["overtime"] = true;
          }
          axios.post("https://localhost/borrowHistory/getBookidByBarcode",qs.stringify({barcode:element.barcode})).then(res=>{
            element["bookid"] = res.data;
            axios.post("https://localhost/book/getBookById",qs.stringify({bookid:element.bookid})).then(res=>{
              element["bookname"] = res.data.bookname;
            })
          })
        })
      })
    },
    changeLossState(borrowid){
      axios.post("https://localhost/borrowHistory/changeLossState",qs.stringify({borrowid:borrowid})).then(res=>{
        if(res.data == 1){
          this.reload();
        }
      })
    },
    goToDetail(row){
     window.open("https://localhost/bookDetail?keyword="+encodeURI(row.bookid));
    }
  },
  created(){
    let token = JSON.parse(localStorage.getItem("token"));
    this.userid = token.id;
    axios.post('https://localhost/borrowHistory/getCurrentBorrowingByUserid',qs.stringify({userid:this.userid})).then(res=>{
      this.tableData = res.data;
      this.tableData.forEach(element=>{
        element.borrowtime = new Date(element.borrowtime);
        element.duedate = new Date(element.duedate);
        let now = Date.parse(Date());
        if(now < Date.parse(element.duedate)){
          element["overtime"] = false;
        }
        else{
          element["overtime"] = true;
        }
        axios.post("https://localhost/borrowHistory/getBookidByBarcode",qs.stringify({barcode:element.barcode})).then(res=>{
          element["bookid"] = res.data;
          axios.post("https://localhost/book/getBookById",qs.stringify({bookid:element.bookid})).then(res=>{
            element["bookname"] = res.data.bookname;
          })
        })
      })
    })
  }
}
</script>