<template>
  <div style="width: 100%;height: 100%">
    <el-form label-width="auto" :label-position="right" :model="form" size="large" style="margin-left:20px">
      <el-form-item label="id:">
          <span>{{userInfo.id}}</span>
      </el-form-item>
      <el-form-item label="用户名:">
          <el-input style="width:20%" placeholder="请输入内容" v-show="show" v-model="userInfo.username"></el-input>
          <span v-show="!show">{{userInfo.username}}</span>
      </el-form-item>
      <el-form-item label="性别:">
          <el-radio-group v-model="userInfo.sex" v-if="show">
            <el-radio label="男">男</el-radio>
            <el-radio label="女">女</el-radio>
            <el-radio label="秘密">秘密</el-radio>
          </el-radio-group>
          <span v-show="!show">{{userInfo.sex}}</span>
      </el-form-item>
      <el-form-item label="生日:">
          <el-date-picker
            v-if="show"
            v-model="userInfo.birthday"
            type="date"
            placeholder="选择您的生日"
            :disabled-date="disabledDate"
            :shortcuts="shortcuts"
          />
          <span v-show="!show">{{userInfo.birthday.getFullYear()}}年{{userInfo.birthday.getMonth()+1}}月{{userInfo.birthday.getDate()}}日</span>
      </el-form-item>
      <el-form-item label="邮箱:">
          <el-input style="width:20%" placeholder="请输入内容" v-show="show" v-model="userInfo.email"></el-input>
          <span v-show="!show">{{userInfo.email}}</span>
      </el-form-item>
      <el-form-item label="手机号码:">
          <el-input style="width:20%" placeholder="请输入内容" v-show="show" v-model="userInfo.phonenum"></el-input>
          <span v-show="!show">{{userInfo.phonenum}}</span>
      </el-form-item>
      <el-form-item label="个人简介:">
          <el-input :autosize="{ minRows: 2, maxRows: 4 }" type="textarea" style="width:20%" placeholder="请输入内容" v-show="show" v-model="userInfo.profile"></el-input>
          <span v-show="!show">{{userInfo.profile}}</span>
      </el-form-item>
      <el-form-item>
        <el-button  size="large" v-show="!show" @click="show=true">编辑</el-button>
        <el-button  size="mini" v-show="show" @click="saveEdit();show=false">保存</el-button>
      </el-form-item>
    </el-form>
  </div>
</template>

<script>
import Header from "@/components/Header";
import Aside from "@/components/Aside";
import axios from "axios";
import qs from "qs";
export default {
  name: 'HomeView',
  components: {
    Header,
    Aside
  },
  data(){
    return{
      userInfo:{birthday:new Date()},
      show:false,
    }
  },
  methods:{
    saveEdit(){
      axios.post('https://localhost/student/edit',qs.stringify(this.userInfo));
    },
  },
  created(){
    let token = JSON.parse(localStorage.getItem('token'));
    let id = token.id;
    axios.post('https://localhost/student/getStudent',qs.stringify({id:id})).then(res=>{
      this.userInfo = res.data;
      this.userInfo.birthday = new Date(res.data.birthday);
    })
  }
}
</script>
