<template>
    <el-row align="middle">
        <el-col style="font-weight:bold;font-size:20px" :span="2">
          {{userInfo.username}}
          <el-button style="margin-top:10px" size="middle" @click="test">个人信息</el-button>
        </el-col>
        <el-col :span="2">
          <el-card style="width:100px;height:100px;">
              <div style="font-size:13px">最多可借</div>
              <div style="margin-top:23px;font-size:25px">{{userInfo.maxborrowing}}</div>
          </el-card>
        </el-col>
    </el-row>
    <el-row align="middle">
      <el-card style="width:200px;height:150px;margin-right:20px">
          <div style="font-size:20px">可借图书</div>
          <div style="margin-top:20px;font-size:40px;margin-right:20px">{{userInfo.availableborrowing}}</div>
      </el-card>
      <el-card style="width:200px;height:150px;margin-right:20px">
          <div style="font-size:20px">未还图书</div>
          <div style="margin-top:20px;font-size:40px;">{{userInfo.currentborrowing}}</div>
      </el-card>
      <el-card style="width:200px;height:150px;margin-right:20px">
          <div style="font-size:20px">超时图书</div>
          <div style="margin-top:20px;font-size:40px">{{userInfo.overtimeborrowing}}</div>
      </el-card>
      <el-card style="width:200px;height:150px;margin-right:20px">
          <div style="font-size:20px">未缴费挂失图书</div>
          <div style="margin-top:20px;font-size:40px;">{{userInfo.loss}}</div>
      </el-card>
    </el-row>
</template>

<script>
import Header from "@/components/Header";
import Aside from "@/components/Aside";
import axios from "axios";
import qs from "qs";
export default {
  name: 'HomeView',
  components: {
    Header,
    Aside
  },
  data(){
    return{
      userInfo:{},
      show:false
    }
  },
  methods:{
    test(){
        this.$router.push("/personalInfo")
    },
    saveEdit(){
      axios.post('https://localhost/student/edit',qs.stringify(this.userInfo))
    }
  },
  created(){
    let token = JSON.parse(localStorage.getItem('token'));
    let id = token.id;
    axios.post('https://localhost/student/getStudent',qs.stringify({id:id})).then(res=>{
      this.userInfo = res.data;
      axios.post('https://localhost/borrowHistory/getCurrentBorrowingByUserid',qs.stringify({userid:this.userInfo.id})).then(res=>{
        this.userInfo["availableborrowing"] = this.userInfo.maxborrowing;
        this.userInfo["currentborrowing"] = 0;
        this.userInfo["overtimeborrowing"] = 0;
        this.userInfo["loss"] = 0;
        let borrowHistories = [];
        borrowHistories = res.data;
        borrowHistories.forEach(element => {
          let now = new Date();
          let duedate = new Date(element.duedate);
          this.userInfo.currentborrowing++;
          if(element.returntime == null){
            this.userInfo.availableborrowing--;
          }
          if(Date.parse(now) > Date.parse(duedate)){
            this.userInfo.overtimeborrowing++;
          }
          if(element.loss == 1){
            this.userInfo.loss++;
          }
        });
      })
    })
  }
}
</script>
