<template>
  <div>
    <el-form label-width="auto" :label-position="right" ref="form" :rules="rules" :model="userInfo" size="large">
      <el-form-item label="旧密码" prop="old">
          <el-input show-password style="width:20%" placeholder="旧密码" v-model="userInfo.old"></el-input>
      </el-form-item>
      <el-form-item label="新密码:" prop="new">
          <el-input show-password style="width:20%" placeholder="请输入新密码" v-model="userInfo.new"></el-input>
      </el-form-item>
      <el-form-item label="确认新密码:" prop="confirm">
          <el-input show-password style="width:20%" placeholder="请再次输入新密码" v-model="userInfo.confirm"></el-input>
      </el-form-item>
    </el-form>
    <el-button @click="changePassword">修改密码</el-button>
  </div>
</template>

<script>
import Header from "@/components/Header";
import Aside from "@/components/Aside";
import axios from "axios";
import qs from "qs";
export default {
  name: 'HomeView',
  components: {
    Header,
    Aside
  },
  data(){
    return{
      userInfo:{
        id:undefined,
        old:undefined,
        new:undefined,
        confirm:undefined
      },
      rules: {
        old: [{ required: true, message: '不能为空', trigger: 'blur' },],
        new: [{ required: true, message: '不能为空', trigger: 'blur' },],
        confirm:[{ required: true, message: '不能为空', trigger: 'blur' }],
      },
    }
  },
  methods:{
    changePassword(){
      this.$refs.form.validate(valid=>{
        if(valid){
          if(this.userInfo.new == this.userInfo.confirm){
            axios.post('https://localhost/student/changePassword',qs.stringify(this.userInfo)).then(res=>{
              if(res.data == true){
                this.$message({
                              type:"success",
                              message:"修改密码成功！"
                            })
              }
              else{
                this.$message({
                              type:"error",
                              message:"修改密码失败"
                            })
              }
            })
          }
          else{
            this.$message({
              type:"error",
              message:"两次输入密码不同"
            })
          }
        }
        else{
          this.$message({
            type:"error",
            message:"请检查输入"
          })
        }
      })
    }
  },
  created(){
    let token = JSON.parse(localStorage.getItem('token'));
    let id = token.id;
    this.userInfo.id = id;
  }
}
</script>
