import { createRouter, createWebHashHistory } from 'vue-router'
import PersonalInfo from '@/views/PersonalInfo.vue'
import BorrowHistoryView from '@/views/BorrowHistoryView.vue'
import CurrentBorrowing from "@/views/CurrentBorrowing.vue"
import BookShelf from "@/views/BookShelf.vue"
import Home from "@/views/Home.vue"
import SafeCenter from "@/views/SafeCenter"
const routes = [
  {
    path: '/borrowHistory',
    name: 'bookView',
    component: BorrowHistoryView,
  },
  {
    path: '/safeCenter',
    name: 'safeCenter',
    component: SafeCenter,
  },
  {
    path:'/personalInfo',
    name:'personalInfo',
    component:PersonalInfo
  },
  {
    path:'/currentBorrowing',
    name:'currentBorrowing',
    component:CurrentBorrowing
  },
  {
    path:'/bookShelf',
    name:'studentView',
    component:BookShelf
  },
  {
    path:'/',
    name:'home',
    component:Home
  },
]

const router = createRouter({
  history: createWebHashHistory(process.env.BASE_URL),
  routes
})

export default router
