<template>
    <div>
        <el-dialog v-model="editFormVisible" title="Shipping address">
            <el-form :model="editFormData">
                <el-form-item label="书架名" :label-width="formLabelWidth">
                    <el-input v-model="editFormData.bookshelfname" autocomplete="off" />
                </el-form-item>
                <el-form-item label="书架简介" :label-width="formLabelWidth">
                    <el-input :autosize="{ minRows: 2, maxRows: 4 }" type="textarea" placeholder="请输入内容" v-model="editFormData.bookshelfprofile"></el-input>
                </el-form-item>
            </el-form>
            <template #footer>
                <span class="dialog-footer">
                    <el-button @click="editFormVisible = false">取消</el-button>
                    <el-button type="primary" @click="editConfirm">确认</el-button>
                </span>
            </template>
        </el-dialog> 
        <el-button @click="addNewShelf" size="large" style="width:100%">
            新建书架
        </el-button>
        <el-menu
            class="el-menu-vertical-demo"
            style="height:100%;width:100%"
            :collapse-transition = "false"
        >
        <el-menu-item
            :index="item.id"
            v-for="item in bookShelves"
            :key="item.id"
            @click="getBook(item)">
            <template #title>
                {{item.name}}
                <el-dropdown :hide-on-click="false" trigger="click">
                    <el-button size="small">
                        操作
                    </el-button>
                    <template #dropdown>
                        <el-dropdown-menu>
                            <el-dropdown-item @click.stop="editShelf(item)">编辑</el-dropdown-item>
                            <el-dropdown-item>
                                <el-popconfirm
                                confirm-button-text="OK"
                                cancel-button-text="No, Thanks"
                                :icon="InfoFilled"
                                icon-color="red"
                                title="确认删除该书架吗？"
                                @confirm.stop="deleteShelf(item.id)">
                                    <template #reference>
                                        删除
                                    </template>
                                </el-popconfirm>
                            </el-dropdown-item>
                        </el-dropdown-menu>
                    </template>
                </el-dropdown>
            </template>
        </el-menu-item>
        </el-menu>
    </div>
</template>

<script>
import axios from 'axios';
import qs from "qs"
export default {
    name: "Aside",
    data() {
        return {
            tabledata:[],
            id:undefined,
            bookShelves:[],
            currentBookshelfid:undefined,
            editFormData:{
                bookshelfid:undefined,
                bookshelfname:undefined,
                bookshelfprofile:undefined
            },
            editFormVisible:false
        }
    },
    methods:{
        addNewShelf(){
            this.$emit("addNewShelf")
        },
        deleteShelf(bookshelfid){
            axios.post("https://localhost/bookshelf/deleteBookshelfByid",qs.stringify({bookshelfid:bookshelfid})).then(res=>{
                if(res.data == true){
                    this.$message({
                    type:"success",
                    message:"删除成功"
                    })
                    this.reloadShelves();
                    this.$emit("reloadTable")
                }
                else{
                    this.$message({
                        type:"error",
                        message:"删除失败"
                    })
                }
            })
        },
        editShelf(bookshelf){
            this.editFormVisible = true;
            this.editFormData.bookshelfid = bookshelf.id;
            this.editFormData.bookshelfname = bookshelf.name;
            this.editFormData.bookshelfprofile = bookshelf.profile;
        },
        editConfirm(){
            axios.post("https://localhost/bookshelf/editBookshelf",qs.stringify(this.editFormData)).then(res=>{
                if(res.data == true){
                    this.$message({
                        type:"success",
                        message:"编辑成功"
                    })
                    this.editFormVisible = false;
                    this.reloadShelves();
                }
                else{
                    this.$message({
                        type:"error",
                        message:"编辑失败"
                    })
                }
            })
        },
        reloadShelves(){
            let token = JSON.parse(localStorage.getItem('token'));
            this.id = token.id;
            axios.post('https://localhost/bookshelf/getAllBookshelfByStudentid',qs.stringify(token)).then(res=>{
                this.bookShelves = res.data;
            })
        },
        getBook(item){
            this.currentBookshelfid = item.id;
            axios.post('https://localhost/bookshelf/getAllBookByBookshelfid',qs.stringify({bookshelfid:item.id})).then(res=>{
                this.$emit("changeShelf",res)
            })
        }
    },
    created(){
        let token = JSON.parse(localStorage.getItem('token'));
        this.id = token.id;
        axios.post('https://localhost/bookshelf/getAllBookshelfByStudentid',qs.stringify(token)).then(res=>{
            this.bookShelves = res.data;
        })
    }
};
</script>

